body {
  background-color: #f9f9f9;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  background: #fff;
  border-radius: 8px;
  max-width: 1160px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 4px 6px #0000001a;
}

h1, h2 {
  text-align: center;
}

table {
  border-collapse: collapse;
  width: 100%;
  margin: 20px 0;
}

th, td {
  text-align: center;
  border: 1px solid #ddd;
  padding: 8px;
}

th {
  background-color: #f4f4f4;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  padding: 8px 12px;
}

.delete-btn {
  background-color: #dc143c;
}

.delete-btn:hover {
  background-color: brown;
}

button:hover {
  background-color: #0056b3;
}

form {
  margin-top: 20px;
}

form label {
  margin-bottom: 8px;
  display: block;
}

form input {
  box-sizing: border-box;
  width: 100%;
  margin-bottom: 10px;
  padding: 6px;
}

.backdrop {
  z-index: 2;
  visibility: hidden;
  background-color: #00000067;
  justify-content: center;
  align-items: center;
  display: none;
  position: fixed;
  inset: 0;
}

.modal {
  z-index: 3;
  background: #fff;
  border-radius: 8px;
  flex-direction: column;
  align-items: center;
  margin: 20px auto;
  padding: 20px;
  display: flex;
  box-shadow: 0 4px 6px #0000001a;
}
